<template>
    <v-card>
        <v-card-title>Add Security</v-card-title>
        <v-card-text>
            <v-form ref="form" v-model="formHasErrors">
                <v-text-field ref="name"
                    autocomplete="off"
                    :rules="[rules.required]"
                    v-model="security.name"
                    label="Owner"
                    placeholder="Bruce Wayne">
                </v-text-field>
                <v-text-field ref="account"
                    autocomplete="off"
                    :rules="[rules.required]"
                    v-model="security.account"
                    label="Account"
                    placeholder="55ZZZ88A">
                </v-text-field>
                <v-text-field ref="security"
                    autocomplete="off"
                    :rules="[rules.required]"
                    v-model="security.security"
                    label="Security"
                    placeholder="Wayne Industries">
                </v-text-field>
                <v-text-field ref="symbol"
                    autocomplete="off"
                    :rules="[rules.required]"
                    v-model="security.symbol"
                    label="Symbol"
                    placeholder="WAYNE">
                </v-text-field>
                <v-text-field ref="book_value"
                    autocomplete="off"
                    :rules="[rules.required, rules.number]"
                    v-model="security.book_value"
                    label="Book Value ($)"
                    placeholder="12456.22">
                </v-text-field>
                <v-text-field ref="market_value"
                    autocomplete="off"
                    :rules="[rules.required, rules.number]"
                    v-model="security.market_value"
                    label="Market Value ($)"
                    placeholder="24567.33">
                </v-text-field>
            </v-form>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat @click="clear">clear</v-btn>
            <v-btn :disabled="!formHasErrors" color="primary" @click="submit">Add</v-btn>
        </v-card-actions>
    </v-card>
</template>
<script>
import utilsMixin from '@/utils/utilsMixin'
export default {
    mixins: [utilsMixin],
    props: {
        security: {
            type: Object,
            default() {
                return {
                    name: '',
                    account: '',
                    security: '',
                    symbol: '',
                    quantity: '',
                    market_value: '',
                    book_value: ''
                }
            }
        }
    },
    data: () => ({
        formHasErrors: false
    }),
    
    methods: {
        clear() {
            this.$refs.form.reset()
        },
        submit() {
            if(this.$refs.form.validate()) {
                let security = {...this.security}
                this.$store.commit('portfolio/addOrUpdateSecurity', security)
                this.$store.dispatch('notification/success')
                this.clear()
            }
        }
    }
}
</script>
<style>

</style>



