export default function* getLinesGen(ws) {
    let wsRange = getWorksheetRange(ws)
    let data = zipWithHeaders(getCells(ws), wsRange)
    for(let i = wsRange.lower.row + 1; i < wsRange.upper.row; i++) {
        yield data.filter(c => c.address.row === i)
    }
    return
}

function zipWithHeaders(cells, wsRange) {
    let headers = cells.filter(c => c.address.row === wsRange.lower.row)
    let data = cells.filter(c => c.address.row !== wsRange.lower.row)
    let byColumn = {}
    headers.forEach(h => byColumn[h.address.col] = h)
    return data.map(c => {
        c.header = byColumn[c.address.col]
        return c
    })
}
function getWorksheetRange(ws) {
    let [lower, upper] = ws['!ref'].split(':')
    return {
        text: ws['!ref'],
        lower: decodeAddress(lower),
        upper: decodeAddress(upper)
    }
}

function getCells(ws) {
    return Object.entries(ws)
        .filter(([k, v]) => !k.includes('!'))
        .map(([k, v]) => {
            v.address = decodeAddress(k)
            return v
        })
        .sort((a, b) => a.address.text - b.address.text)
}

function decodeAddress(address) {
    return {
        text: address,
        col: decodeCol(address),
        row: decodeRow(address)
    }
}

function decodeCol(address) {
    let col = address.replace(/[0-9]/g, '').split('')
    return col.map(l => l.toLowerCase().charCodeAt(0) - 'a'.charCodeAt(0))
        .reduce((acc, cur) => acc + cur, 0)
}

function decodeRow(address) {
    return parseInt(address.replace(/[a-z]/gi, '')) - 1
}



