<template>
    <v-card>
        <v-card-title>Add Group</v-card-title>
        <v-card-text>
            <v-form ref="form" v-model="formHasErrors">
                <v-text-field ref="name"
                    autocomplete="off"
                    :rules="[rules.required]"
                    v-model="group.name"
                    label="Name"
                    placeholder="Connected Wealth"
                    required>
                </v-text-field>
                <v-text-field ref="tag"
                    autocomplete="off"
                    :rules="[rules.required]"
                    v-model="group.tag"
                    label="Tag"
                    placeholder="Risky"
                    required>
                </v-text-field>
            </v-form>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat @click="clear">clear</v-btn>
            <v-btn :disabled="!formHasErrors" color="primary" @click="submit">Add</v-btn>
        </v-card-actions>
    </v-card>
</template>
<script>
import utilsMixin from '@/utils/utilsMixin'
export default {
    mixins: [utilsMixin],
    props: {
        group: {
            type: Object,
            default() {
                return {
                    name: '',
                    tag: ''
                }
            }
        }
    },
    data: () => ({
        formHasErrors: false
    }),
    methods: {
        clear() {
            this.$refs.form.reset()
        },
        submit() {
            if(this.$refs.form.validate()) {
                let group = {...this.group}
                this.$store.commit('model/addOrUpdateGroup', group)
                this.$store.dispatch('notification/success')
                this.clear()
            }
        }
    }
}
</script>
<style>

</style>



