<template>
    <v-card >
        <v-card-title>
            Securities
            <v-spacer></v-spacer>
            <v-text-field v-model="search" append-icon="search" label="Search" single-line hide-details></v-text-field>
        </v-card-title>
        <v-card-text>
            <v-data-table :headers="headers" :items="items" :search="search">
                <template slot="items" slot-scope="props">
                    <td> {{props.item.name | shorten}} </td>
                    <td> {{props.item.account | shorten}} </td>
                    <td> {{props.item.security | shorten}} </td>
                    <td> {{props.item.symbol | shorten }} </td>
                    <td> {{props.item.quantity | shorten }} </td>
                    <td> {{props.item.book_value | shorten}} </td>
                    <td> {{props.item.market_value | shorten }} </td>
                    <td>
                        <v-icon small @click="editItem(props.item)">edit</v-icon>
                        <v-icon small @click="deleteItem(props.item)">delete</v-icon>
                    </td>
                </template>
                <template slot="no-data">
                    Your model is currently empty
                </template>
                <template slot="no-results">
                    Your search for "{{search}}" found no results.
                </template>
            </v-data-table>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat @click="load">Load</v-btn>
            <v-dialog v-model="dialog" max-width="500px">
                <v-btn slot="activator" @click="addItem" color="primary">Add Item</v-btn>
                <add-security :security="security"></add-security>
            </v-dialog>
        </v-card-actions>
    </v-card>
</template>
<script>
import * as XLSX from 'xlsx'
import utilsMixin from '@/utils/utilsMixin'
import AddSecurity from '@/components/AddSecurity'
export default {
    mixins: [utilsMixin],
    components: {
        'add-security': AddSecurity
    },
    data: () => ({
        dialog: false,
        search: '',
        security: {},
        headers: [
            {
                text: 'Owner',
                align: 'left',
                sortable: true,
                value: 'name'
            },
            {
                text: 'Account',
                align: 'left',
                sortable: true,
                value: 'account'
            },
            {
                text: 'Security',
                align: 'left',
                sortable: true,
                value: 'security'
            },
            {
                text: 'Symbol',
                align: 'left',
                sortable: true,
                value: 'symbol'
            },
            {
                text: 'Quantity',
                align: 'left',
                sortable: true,
                value: 'quantity'
            },
            {
                text: 'Book Value ($)',
                align: 'left',
                value: 'book_value'
            },
            {
                text: 'Market Value ($)',
                align: 'left',
                value: 'market_value'
            },
            {
                text: 'Actions',
                align: 'left',
                value: 'market_value'
            }
        ],
    }),
    computed: {
        items() {
            return this.$store.getters['portfolio/getSecurities']
                .map(x => Object.assign({}, x))
        },
    },
    methods: {
        editItem(item) {
            this.security = {...item}
            this.dialog = true
        },
        addItem() {
            this.security = {}
            this.dialog = true
        },
        deleteItem(item) {
            this.$store.commit('portfolio/deleteSecurity', item.id)
        },
        load() {
            let input = document.createElement('input')
            input.setAttribute('type', 'file')
            input.setAttribute('accept', '.xlsx')
            input.onchange = () => {
                let reader = new FileReader()
                reader.onabort = () => {console.log('abort on load')}
                reader.onerror = () => {console.log('error on load')}
                reader.onload = (evt) => {
                    try {
                        const bstr = evt.target.result
                        const wb = XLSX.read(bstr, {type:'binary'})
                        const ws = wb.Sheets[wb.SheetNames[0]]
                        this.$store.dispatch('portfolio/parseWorksheet', ws)
                        this.$store.dispatch('notification/success')
                    }
                    catch(err) {
                        this.$store.dispatch('notification/error')
                    }
                }
                if(input.files.length > 0) reader.readAsBinaryString(input.files[0])
            }
            input.click()
        }
    },
}
</script>
<style>
</style>


