<template>
    <v-card >
        <v-card-title>
            Groups
            <v-spacer></v-spacer>
            <v-text-field v-model="search" append-icon="search" label="Search" single-line hide-details></v-text-field>
        </v-card-title>
        <v-card-text>
            <v-data-table :headers="headers" :items="items" :search="search">
                <template slot="items" slot-scope="props">
                    <td> {{props.item.name | shorten}} </td>
                    <td> {{props.item.tag | shorten}} </td>
                    <td>
                        <v-icon small @click="editItem(props.item)">edit</v-icon>
                        <v-icon v-if="!props.item.default" small @click="deleteItem(props.item)">delete</v-icon>
                    </td>
                </template>
                <template slot="no-data">
                    Your model is currently empty
                </template>
                <template slot="no-results">
                    Your search for "{{search}}" found no results.
                </template>
            </v-data-table>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
                <v-btn slot="activator" @click="addItem" color="primary">Add Item</v-btn>
                <add-group :group="group"></add-group>
            </v-dialog>
        </v-card-actions>
    </v-card>
</template>
<script>
import utilsMixin from '@/utils/utilsMixin'
import AddGroup from '@/components/AddGroup'
export default {
    mixins: [utilsMixin],
    components: {
        'add-group': AddGroup
    },
    data: () => ({
        dialog: false,
        search: '',
        group: {},
        headers: [
            {
                text: 'Name',
                align: 'left',
                sortable: true,
                value: 'name'
            },
            {
                text: 'Tag',
                align: 'left',
                sortable: true,
                value: 'tag'
            },
            {
                text: 'Actions',
                align: 'left',
                value: 'identifiers'
            }
        ],
    }),
    computed: {
        items() {
            return this.$store.state.model.groups
                .map(x => Object.assign({}, x))
        }
    },
    methods: {
        editItem(item) {
            this.group = {...item}
            this.dialog = true
        },
        addItem() {
            this.group = {}
            this.dialog = true
        },
        deleteItem(item) {
            this.$store.commit('model/deleteGroup', item.id)
        }
    },
}
</script>
<style>
.v-table__overflow {
    overflow: hidden;
}
</style>



