export default {
    data: () => ({
        rules: {
            required: (v) => !!v || 'This field is required',
            number: (v) => !isNaN(+v) || 'This field must be a number',
            commaString: (v) => {
                let result = 'This field must be a well-formed comma delimitted string'
                if(v) {
                    let exec = v.match(/(\w\s?)+(\s*,\s*(\w\s?)+)*/i)
                    if(exec) result = exec[0].length == v.length ? true : result
                }
                return result
            }
        }
    }),
    filters: {
        shorten(v) {
            const LENGTH = 20
            if(v && v.length >= LENGTH) {
                v = v.slice(0, LENGTH - 3) + '...'
            }
            return v
        },
        decimal(v, n) {
            if(typeof v === 'string') {
                v = +v.replace(/[^\d.]/g, '')
                v = isNaN(v) ? 0 : parseFloat(v)
                return v.toFixed(n)
            }
            else if(typeof v === 'number') {
                return v.toFixed(n)
            }
        },
        money(v) {
            return '$ ' + v
        }
    }
}


