import Vue from 'vue'
import Router from 'vue-router'
import Model from '@/pages/Model'
import Portfolio from '@/pages/Portfolio'
import FullRebalance from '@/pages/FullRebalance'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/model',
      name: 'Model',
      component: Model
    },
    {
      path: '/portfolio',
      name: 'Portfolio',
      component: Portfolio
    },
    {
      path: '/full-rebalance',
      name: 'Full Rebalance',
      component: FullRebalance
    },
    {
      path: '*',
      redirect: '/model'
    }
  ]
})



