<template>
<div id="app">
    <v-app id="inspire">
        <v-navigation-drawer fixed clipped class="grey lighten-4" app v-model="drawer" floating>
            <v-list dense class="grey lighten-4">
                <template v-for="(item, i) in items">
                    <v-layout row v-if="item.heading" align-center :key="i">
                        <v-flex xs6>
                            <v-subheader v-if="item.heading">
                                {{ item.heading }}
                            </v-subheader>
                        </v-flex>
                    </v-layout>
                    <v-divider dark v-else-if="item.divider" :key="i" ></v-divider>
                    <v-list-tile :key="i" v-else @click="$router.push(item.path)">
                        <v-list-tile-action>
                            <v-icon>{{ item.icon }}</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title class="grey--text">
                                {{ item.text }}
                            </v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </template>
            </v-list>
        </v-navigation-drawer>
        <v-toolbar app clipped-left>
            <v-toolbar-side-icon @click.native="drawer = !drawer"></v-toolbar-side-icon>
            <span class="title ml-3 mr-5">Blue&nbsp;<span class="text">Sheets</span></span>
            <v-spacer></v-spacer>
        </v-toolbar>
        <v-content>
            <v-container fluid fill-height class="grey lighten-4">
                <v-layout column fill-height >
                    <v-slide-y-transition>
                        <router-view></router-view>
                    </v-slide-y-transition>
                </v-layout>
            </v-container>
            <v-snackbar v-model="snackVisible" :timeout="snackbar.timeout" :color="snackbar.color" bottom right  :multi-line="snackbar.multiline">
                {{ snackbar.text }}
                <v-btn flat @click="hideSnack">Close</v-btn>
            </v-snackbar>
        </v-content>
        <system-bar></system-bar>
    </v-app>
</div>
</template>

<script>
import SystemBar from '@/components/SystemBar'
export default {
    components: {
        'system-bar': SystemBar
    },
    name: 'App',
    data: () => ({
        drawer: null,
        text: '',
        items: [
            { heading: 'Rebalance' },
            { icon: 'fingerprint', text: 'Model', path: '/model'},
            { icon: 'attach_money', text: 'Portfolio', path: '/portfolio'},
            { icon: 'reply_all', text: 'Full Rebalance', path: '/full-rebalance'},
            { divider: true },
            { heading: 'Other' },
            { icon: 'settings', text: 'Settings' },
            { icon: 'help', text: 'Help' }
        ]
    }),
    computed: {
        snackbar() {
            return this.$store.state.notification
        },
        snackVisible: {
            get: function() {
                return this.$store.state.notification.visible
            },
            set: function(v) {
                this.$store.commit('notification/hide', null)
            }
        },
    },
    methods: {
        hideSnack() {
            this.$store.commit('notification/hide', null)
        }
    }
}
</script>
<style>

</style>



