<template>
    <v-card>
        <v-card-title>Add Definition / {{totalWeight}}%</v-card-title>
        <v-card-text>
            <v-form ref="form" v-model="formHasErrors">
                <v-text-field ref="name"
                    autocomplete="off"
                    :rules="[rules.required]"
                    v-model="definition.name"
                    label="Name"
                    placeholder="Canada Natural Resources">
                </v-text-field>
                <v-text-field ref="category"
                    autocomplete="off"
                    :rules="[rules.required]"
                    v-model="definition.category"
                    label="Category"
                    placeholder="Equity">
                </v-text-field>
                <v-text-field ref="weight"
                    autocomplete="off"
                    :rules="[rules.required, rules.number]"
                    v-model="definition.weight"
                    label="Weight (%)"
                    placeholder="12.37">
                </v-text-field>
                <v-text-field ref="identifiers"
                    autocomplete="off"
                    :rules="[rules.required, rules.commaString]"
                    v-model="definition.identifiers"
                    label="Identifiers / CUSIPs"
                    placeholder="CRNL, 123456789">
                </v-text-field>
                <v-select ref="group"
                    :items="groups"
                    item-text="name"
                    item-value="id"
                    return-object
                    v-model="group"
                    label="Group">
                </v-select>
            </v-form>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat @click="clear">clear</v-btn>
            <v-btn :disabled="!formHasErrors" color="primary" @click="submit">Add</v-btn>
        </v-card-actions>
    </v-card>
</template>
<script>
import utilsMixin from '@/utils/utilsMixin'
export default {
    mixins: [utilsMixin],
    props: {
        definition: {
            type: Object,
            default() {
                return {
                    name: '',
                    category: '',
                    weight: '',
                    identifiers: ''
                }
            }
        }
    },
    data: () => ({
        formHasErrors: false,
        group: {},
    }),
    computed: {
        groups() {
            return this.$store.state.model.groups
        },
        totalWeight() {
            return this.$store.getters['model/totalWeight']
        }
    },
    beforeMount() {
        this.group = this.$store.state.model.groups[0]
    },
    methods: {
        clear() {
            this.$refs.form.reset()
            this.$refs.group.selectItem(this.$store.state.model.groups[0])
        },
        submit() {
            if(this.$refs.form.validate()) {
                let definition = {...this.definition, groupId: this.group.id}
                this.$store.commit('model/addOrUpdateDefinition', definition)
                this.$store.dispatch('notification/success')
                this.clear()
            }
        }
    }
}
</script>
<style>

</style>



