<template>
    <v-container fluid grid-list-md>
        <v-layout row wrap>
            <v-flex xs12 sm6 md4 lg3 xl2 v-for="(item, i) in accounts" :key="i">
                <account-summary :account="item"></account-summary>
            </v-flex>
        </v-layout>
    </v-container>
</template>
<script>
import AccountSummary from '@/components/AccountSummary'
export default {
    components: {
        'account-summary': AccountSummary
    },
    data: () => ({
    }),
    computed: {
        accounts() {
            let accounts = this.$store.state.portfolio.securities
                .map(s => s.account)
                .filter((a, index, self) => self.indexOf(a) === index)
                .map(a => this.accountSummary(a))
            return accounts
        },
        modelIsComplete() {
            return this.$store.getters['model/totalWeight'] === 100
        }
    },
    methods: {
        accountSummary(account) {
            const securities = this.$store.state.portfolio.securities
                .filter(s => s.account === account)
            const value = securities.reduce((acc, cur) => acc + cur.market_value, 0)
            const name = securities[0].name
            const type = this.getAccountType(account)
            return {
                account,
                value,
                name,
                type
            }
        },
        getAccountType(account) {
            let type = 'unknown'
            let t = account.charAt(account.length - 1).toLowerCase()
            if('ae'.indexOf(t) >= 0) type = 'Non Reg'
            else if('r' == t) type = 'RSP'
            else if('s' == t) type = 'Spousal'
            else if('v' == t) type = 'LIRA'
            else if('t' == t) type = 'RIF'
            else if('j' == t) type = 'TFSA'
            else if('w' == t) type = 'LIF'
            else if('u' == t) type = 'Spousal RIF'
            else if('b' == t) type = 'USD'
            else if('z' == t) type = 'RESP'
            return type
        }
    }
}
</script>

<style>
</style>



