const state = {
    text: 'I\'m a snackbar!',
    color: 'green',
    visible: false,
    multiline: false,
    timeout: 3000
}

const mutations = {
    show(state, snackbar) {
        state.text = snackbar.text || 'Hi there!'
        state.color = snackbar.color || 'primary'
        state.timeout = snackbar.timeout || state.timeout
        state.multiline = snackbar.text > 50
        state.visible = true
    },
    hide(state, _) {
        if(state.visible) state.visible = false
    }
}

const actions = {
    defaultSnack(context, {text}) {
        context.commit('show', {
            text: text
        })
    },
    warn(context, {text}) {
        context.commit('show', {
            text: text,
            color: 'warning'
        })
    },
    info(context, {text}) {
        context.commit('show', {
            text: text,
            color: 'info'
        })
    },
    error(context) {
        context.commit('show', {
            text: 'Error!',
            color: 'error'
        })
    },
    success(context) {
        context.commit('show', {
            text: 'Success!',
            color: 'success'
        })
    }
}

const getters = {
   
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}



