import {uuidv4, validateId, upsert, parseFloats} from '@/utils/storeUtils'
import getLinesGen from '@/utils/worksheetParser'
import Vue from '@/../node_modules/vue';

const state = {
    securities: []
}

const mutations = {
    addOrUpdateSecurity(state, security) {
        validateId(security)
        parseFloats(security)
        combine(state, security) || upsert(state.securities, security)
    },
    clear() {
        state.securities = []
    },
    deleteSecurity(state, id) {
        let security = state.securities.find(s => s.id === id)
        if(security) {
            const index = state.securities.indexOf(security)
            state.securities.splice(index, 1)
        }
    }
}

const actions = {
    parseWorksheet(context, ws) {
        context.commit('clear')
        let gen = getLinesGen(ws)
        let yielded = gen.next()
        while(!yielded.done) {
            context.dispatch('parseRow', yielded.value)
            yielded = gen.next()
        }
        
    },
    parseRow(context, cells) {
        let security = {}
        cells.forEach(c => {
            let parsed
            switch(c.t) {
                case 'n':
                    parsed = parseFloat(c.v)
                    break
                case 's':
                    parsed = c.v
                    break
                default:
                    parsed = ''
            }
            security[c.header.v.toLowerCase().trim().replace(' ', '_')] = parsed 
            if(security.cusip) context.commit('addOrUpdateSecurity', security)
        })
    }
}

const getters = {
    getSecurities(state) {
        return state.securities
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}

function combine(state, security) {
    let found = state.securities
        .filter(s => s.account === security.account)
        .filter(s => s.cusip === security.cusip)
        .filter(s => s.id !== security.id)
        .pop()
    if(found) {
        Vue.set(found, 'quantity', found.quantity + security.quantity)
        Vue.set(found, 'book_value', found.book_value + security.book_value)
        Vue.set(found, 'market_value', found.market_value + security.market_value)
    }
    return found ? true : false
}



