<template>
    <v-container grid-list-md>
        <v-layout  row wrap>
            <v-flex xs12>
                <model-add-details></model-add-details>
            </v-flex>
            <v-flex xs12>
                <group-table></group-table>
            </v-flex>
            <v-flex xs12>
                <definition-table></definition-table>
            </v-flex>
        </v-layout>
    </v-container>
</template>
<script>
import GroupTable from '@/components/GroupTable'
import DefinionTable from '@/components/DefinitionTable'
import ModelAddDetails from '@/components/ModelAddDetails'

export default {
    components: {
        'definition-table' : DefinionTable,
        'group-table': GroupTable,
        'model-add-details': ModelAddDetails
    }
}
</script>
<style>

</style>



