import {uuidv4, validateId, upsert} from '@/utils/storeUtils'
import Vue from '@/../node_modules/vue';

const state = {
    metaDetails: {
        version: '1.0.0'
    },
    groups: [{
        id: uuidv4(),
        name: 'none',
        tag: 'ungrouped',
        default: true
    }],
    definitions:[]
}

const mutations = {
    updateMetaDetails(state, metaDetails) {
        Object.keys(metaDetails).forEach(k => {
            Vue.set(state.metaDetails, k, metaDetails[k])
        })
    },
    addOrUpdateGroup(state, group) {
        validateId(group)
        upsert(state.groups, group)
    },
    deleteGroup(state, id) {
        let defaultId = state.groups.find(g => g.default).id
        let group = state.groups.find(g => g.id === id)
        if(group && group.id != defaultId) {
            let index = state.groups.indexOf(group)
            state.groups.splice(index, 1)
            state.definitions.forEach(d => d.groupId = defaultId)
        }
    },
    addOrUpdateDefinition(state, definition) {
        validateId(definition)
        validateGroupId(state.groups, definition)
        upsert(state.definitions, definition)
    },
    deleteDefinition(state, id) {
        let index = state.definitions.find(d => d.id === id)
        if(index >= 0) state.definitions.splice(index, 1)
    },
    deserialize(state, payload) {
        let model = JSON.parse(atob(payload))
        state.metaDetails = model.metaDetails
        state.groups = model.groups
        state.definitions = model.definitions
    }
}

const actions = {
    
}

const getters = {
   getDefinitions(state) {       
       return state.definitions.map(d => Object.assign({}, d))
   },
   getGroups(state) {
       return state.groups.map(g => Object.assign({}, g))
   },
   totalWeight(state) {
       return state.definitions.reduce((acc, cur) => acc + parseWeight(cur), 0.0)
   },
   serialize(state) {
       return btoa(JSON.stringify(state))
   }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}

function parseWeight(obj) {
    let weight = obj.weight || 0.0
    if(typeof obj.weight == 'string') {
        weight = +obj.weight.replace(/[^\d.]/g, '')
        weight = isNaN(weight) ? 0 : parseFloat(weight)
    }
    return weight
}

function validateGroupId(groups, definition) {
    let group = groups.find(g => g.id === definition.groupId) || groups.find(g => g.default === true)
    definition.groupId = group.id
}



