<template>
    <v-card>
        <v-card-text class="text-xs-center">
            <h3 class="headline mb-0">{{account.account}} - {{account.type}}</h3>
            <h4>{{account.name}}</h4>
        </v-card-text>
        <v-divider class="mx-3"></v-divider>
        <v-card-text class="text-xs-center">
            <h3 class="headline mb-0">{{account.value | decimal(2) | money}}</h3>
        </v-card-text>
    </v-card>
</template>
<script>
import utilsMixin from '@/utils/utilsMixin'
export default {
    mixins: [utilsMixin],
    props: {
        'account': Object
    }
}
</script>



