<template>
    <v-card >
        <v-card-title>
            Definitions
            <v-spacer></v-spacer>
            <v-text-field v-model="search" append-icon="search" label="Search" single-line hide-details></v-text-field>
        </v-card-title>
        <v-card-text>
            <v-data-table :headers="headers" :items="items" :search="search">
                <template slot="items" slot-scope="props">
                    <td> {{props.item.name | shorten}} </td>
                    <td> {{props.item.category | shorten}} </td>
                    <td> {{props.item.weight | decimal(2) }} </td>
                    <td> {{props.item.identifiers | shorten}} </td>
                    <td>
                        <v-icon small @click="editItem(props.item)">edit</v-icon>
                        <v-icon small @click="deleteItem(props.item)">delete</v-icon>
                    </td>
                </template>
                <template slot="no-data">
                    Your model is currently empty
                </template>
                <template slot="no-results">
                    Your search for "{{search}}" found no results.
                </template>
            </v-data-table>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
                <v-btn slot="activator" @click="addItem" color="primary">Add Item</v-btn>
                <add-definition :definition="definition"></add-definition>
            </v-dialog>
        </v-card-actions>
    </v-card>
</template>
<script>
import utilsMixin from '@/utils/utilsMixin'
import AddDefinition from '@/components/AddDefinition'
export default {
    mixins: [utilsMixin],
    components: {
        'add-definition': AddDefinition
    },
    data: () => ({
        dialog: false,
        search: '',
        definition: {},
        headers: [
            {
                text: 'Name',
                align: 'left',
                sortable: true,
                value: 'name'
            },
            {
                text: 'Category',
                align: 'left',
                sortable: true,
                value: 'category'
            },
            {
                text: 'Weight (%)',
                align: 'left',
                sortable: true,
                value: 'weight'
            },
            {
                text: 'Identifiers / CUSIPs',
                align: 'left',
                sortable: true,
                value: 'identifiers'
            },
            {
                text: 'Actions',
                align: 'left',
                value: 'identifiers'
            }
        ],
    }),
    computed: {
        items() {
            return this.$store.getters['model/getDefinitions']
                .map(x => Object.assign({}, x))
        }
    },
    methods: {
        editItem(item) {
            this.definition = {...item}
            this.dialog = true
        },
        addItem() {
            this.definition = {}
            this.dialog = true
        },
        deleteItem(item) {
            this.$store.commit('model/deleteDefinition', item.id)
        }
    },
}
</script>
<style>
</style>



