import Vue from '@/../node_modules/vue';

export function uuidv4() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}

export function upsert(arr, item) {
    let current = arr.find(x => x.id === item.id)
    if(current) {
        Object.keys(item).forEach(k => Vue.set(current, k, item[k]))
    }
    else {
        arr.push(item)
    }
}

export function validateId(item) {
    item.id = item.id || uuidv4()
}

export function parseFloats(obj) {
    Object.keys(obj).forEach(k => {
        if(typeof obj[k] == 'string' && !/[^\d.-]/gi.test(obj[k])) {
            let f = parseFloat(obj[k])
            obj[k] = isNaN(f) ? 0 : f
        }
    })
}


