<template>
    <v-card>
        <v-card-title>Model Details</v-card-title>
            <v-card-text>
                <v-form ref="form" v-model="formHasErrors">
                    <v-text-field ref="name"
                        autocomplete="off"
                        :rules="[rules.required]"
                        v-model="name"
                        label="Name"
                        placeholder="Untitled-1"
                        required>
                    </v-text-field>
                    <v-text-field ref="author"
                        autocomplete="off"
                        :rules="[rules.required]"
                        v-model="author"
                        label="Author"
                        placeholder="Anonymous"
                        required>
                    </v-text-field>
                </v-form>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat @click="clear">clear</v-btn>
            <v-btn flat color="primary" @click="loadModel">Load</v-btn>
            <v-btn :disabled="!formHasErrors" color="primary" @click="saveModel">Save</v-btn>
        </v-card-actions>
    </v-card>
</template>
<script>
import utilsMixin from '@/utils/utilsMixin'
export default {
    mixins: [utilsMixin],
    data: () => ({
        name: '',
        author: '',
        formHasErrors: false
    }),
    methods: {
        clear() {
            this.$refs.form.reset()
        },
        loadModel() {
            let input = document.createElement('input')
            input.setAttribute('type', 'file')
            input.setAttribute('accept', '.model')
            input.click()
            let reader = new FileReader()
            reader.onabort = () => {console.log('abort on load')}
            reader.onerror = () => {console.log('error on load')}
            reader.onload = () => {
                try {
                    this.$store.commit('model/deserialize', reader.result)
                    this.name = this.$store.state.model.metaDetails.name
                    this.author = this.$store.state.model.metaDetails.author
                    this.$store.dispatch('notification/success')
                }
                catch(err) {
                    this.$store.dispatch('notification/error')
                }
            }
            if(input.files.length > 0) reader.readAsText(input.files[0])
        },
        saveModel() {
            if(this.$refs.form.validate()) {
                this.submit()
                let saveAs =  this.name.replace(/[^A-Z0-9\-]/gi, '').toLowerCase()
                let serialized = this.$store.getters['model/serialize']
                let blob = new Blob([serialized], {type: 'application/text'});
                window.navigator.msSaveBlob(blob, saveAs + '.model')
                this.clear()
            }
        },
        submit() {
            let date = new Date()
            let metaDetails = {
                name: this.name,
                author: this.author,
                createdAt: date.toString()
            }
            this.$store.commit('model/updateMetaDetails', metaDetails)
        }
    }
}
</script>
<style>

</style>



